import React from 'react';
import { FaFacebookSquare, FaTwitter, FaGithub, FaInstagram } from 'react-icons/fa';
import { IconContext } from "react-icons";


import './style.scss'

const Footer = () => {
	return (
		<div className="footer">
			<div className="collumn"><h1>Shortly</h1></div>
			<div className="collumn">
				<div>
					<h4>Reatures</h4>
					<p>Link Shortening</p>
					<p>Branded Links</p>
					<p>Analytics</p>
				</div>
			</div>
			<div className="collumn">
				<div>
					<h4>Resources</h4>
					<p>Blog</p>
					<p>Developers</p>
					<p>Support</p>
				</div>
			</div>
			<div className="collumn">
				<div>
					<h4>Company</h4>
					<p>About</p>
					<p>Out Team</p>
					<p>Careers</p>
					<p>Contact</p>
				</div>
			</div>
			<div className="collumn">
				<IconContext.Provider value={{ color: "hsl(0, 0%, 75%)", size: '1.5rem' }}>
					<div className="social">
						<a href="https://web.facebook.com/felipegomss64">
							<FaFacebookSquare />
						</a>
						<a href="https://twitter.com/felipegomss">
							<FaTwitter />
						</a>
						<a href="https://github.com/felipegomss">
							<FaGithub />
						</a>
						<a href="https://www.instagram.com/felipegomss/">
							<FaInstagram />
						</a>
					</div>
				</IconContext.Provider>
			</div>
		</div>
	);
}

export default Footer;