import React from 'react';
import Short from '../short';
import './style.scss'

const Statistics = () => {
	return (
		<div className="box">
			<div className="short">
				<Short />
			</div>
			<div className="stats">
				<h1 className="title">Advanced Statistics</h1>
				<p>Track how your links are performing across the web with<br /> our advanced statistics dashboard.</p>
				<div className="cardsArea">
					<div className="line">
					</div>
					<div className="card">
						<div className="iconCard">
						</div>
						<h3>Brand Recognition</h3>
						<p>Boost your brand recognition with each click. Genering links don't mean a thing. Branded links help instel confidence in your content.</p>
					</div>
					<div className="card cf">
						<div className="iconCard fa"></div>
						<h3>Detailed Records</h3>
						<p>Gain Insights into who is clicking yout links. Knowing when and where people engagede with your content helps inform better decisions.</p>
					</div>
					<div className="card cs">
						<div className="iconCard sa"></div>
						<h3>Fully Customizable</h3>
						<p>Improve brand awarenesss and content discoverablity through customizable links, supercharging audience engagement.</p>
					</div>
				</div>
			</div>
		</div>
	);
}

export default Statistics;